$color: steelblue;

.my-flix{
    color:$color
}

@media (min-width: 1200px){
div.container {
    max-width: 1440px;
}
}

.nav-link a{
    color:rgba(255,255,255,.75) !important;
}


.director-view img{
    width: 20% !important;
}

.navbar-brand{
    padding: 0.5rem !important;
    background-color: #fcba03;
    align-self: center;
    margin-bottom: 0.3rem;
    border-radius: 5px;
    color: black !important;
    font-weight: 800;
}

div.container{
    padding:0;
    background-color: #343a40;
}

.main-nav{
    background-color: black!important;
}

.wordCountLimited{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    line-clamp: 4;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}

.movieCard{
    margin-bottom: 20px;
}